{/* --- SECOND ROW: Date + Reset + Apply Full Width Same Line --- */}
<Box
  sx={{
    display: "flex",
    gap: 2,
    alignItems: "center",
    width: "100%"
  }}
>
  <Box sx={{ flex: 1 }}>
    <LocalizationProvider dateAdapter={AdapterDateFns}>
      <DateRangePicker
        value={dateRange}
        onChange={(newVal) => {
          setDateRange(newVal);
          const [start, end] = newVal;
          setFilters(prev => ({
            ...prev,
            startDate: start ? start.toISOString() : null,
            endDate: end ? end.toISOString() : null,
            page: 1
          }));
        }}
        slotProps={{
          textField: { size: "small", fullWidth: true }
        }}
      />
    </LocalizationProvider>
  </Box>

  <Button variant="outlined" size="small">
    Reset
  </Button>

  {/* FULL WIDTH Apply button (flex-grow makes it fill remaining space) */}
  <Button
    variant="contained"
    size="small"
    sx={{
      flexGrow: 1,
      height: "40px"
    }}
    onClick={() => onFilterChange && onFilterChange()}
  >
    Apply Filters
  </Button>
</Box>
